import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { LoginService } from 'src/app/services/login.service';
import { getTextures } from '../scripts/draw';

@Component({
  selector: 'app-size-choose',
  templateUrl: './size-choose.component.html',
  styleUrls: ['./size-choose.component.scss'],
})
export class SizeChooseComponent implements OnInit {
  @Output() openCloseMenu = new EventEmitter();
  @Output() beginLevel = new EventEmitter();
  public activeTextureId: number;
  public levelSize: number;
  constructor(private loginService: LoginService) {}

  ngOnInit() {
    console.log(getTextures());
  }

  public startLevel() {
    this.openCloseMenu.emit(-1);
    this.beginLevel.emit({ size: this.levelSize, textureId: this.activeTextureId });
  }

  public logOut(): void {
    this.loginService.logOut();
  }

  public sizes = [10, 20, 30, 40, 50];

  public setActiveTextureId(textureId: number) {
    this.activeTextureId = textureId;
  }

  public getTexturePosition(textureId) {
    return `0px ${-(textureId - 1) * 64}px`;
  }
}
